.navigation-container {
  margin: 20px 10px;
  display: flex;
  justify-content: center;
}
.navigation-body {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border-radius: 7px;
}
.navigation-button {
  cursor: pointer;
  font-size: 14px;
  background-color: whitesmoke;
  border: 3px solid whitesmoke;
  padding: 15px 30px;
}
.navigation-button:hover {
  border-radius: 7px;
  border-bottom: 3px solid purple;
}
