.card-body {
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
  max-width: 350px;
  min-width: 250px;
  height: 100%;
  background-color: white;
  padding: 30px;
  border-radius: 7px;
  margin: 20px;
}
.card-container {
}
.card-top {
  color: gray;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.card-number {
  margin-right: 10px;
}
.card-top-line {
  height: 1px;
  width: 100%;
  background: #00000021;
}
.card-food-type {
  margin-left: 10px;
  font-size: 16px;
}
.card-number-circle {
  border-radius: 50px;
  border: 1px solid gray;
  padding: 1px 6px;
}
.card-food-name {
  margin: 15px 0px;
  font-size: 30px;
  font-weight: bold;
}
.card-food-description {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* number of lines to show */
  -webkit-box-orient: vertical;
  font-size: 15px;
}
.card-food-image {
  margin: 20px 0px;
}
.card-food-image img {
  width: 350px;
  height: 250px;
  border-radius: 7px;
}
.card-order-button {
  border: 1px solid black;
  padding: 6px 12px;
  color: black;
  border-radius: 7px;
}
.card-order-button:hover {
  border: 1px solid black;
  background-color: black;
  color: white;
  border-radius: 7px;
}
.card-order-button span {
  font-weight: bold;
}
